.progressbar-wrapper {
  padding: 25px; background: #fff;
  -moz-border-radius: 4px; -webkit-border-radius: 4px; border-radius: 4px;
  text-align: center;
}

.f1-steps { overflow: hidden; position: relative; margin-top: 20px; }

.f1-progress { position: absolute; top: 24px; left: 0; width: 100%; height: 1px; background: #ddd; }
.f1-progress-line { position: absolute; top: 0; left: 0; height: 1px; background: #466bc8; }

.f1-step { position: relative; float: left; width: 49.99%; padding: 0 5px; }

.f1-step-icon {
  display: inline-block; width: 40px; height: 40px; margin-top: 4px; background: #ddd;
  font-size: 16px; color: #fff; line-height: 40px;
  -moz-border-radius: 50%; -webkit-border-radius: 50%; border-radius: 50%;
}
.f1-step.activated .f1-step-icon {
  background: #fff; border: 1px solid #466bc8; color: #466bc8; line-height: 38px;
}
.f1-step.active .f1-step-icon {
  width: 48px; height: 48px; margin-top: 0; background: #466bc8; font-size: 22px; line-height: 48px;
}

.f1-step p { color: #ccc; }
.f1-step.activated p { color: #466bc8; }
.f1-step.active p { color: #466bc8; }


